<template>
  <article>
    <p>
      <span>{{ "username:" + request.username }}</span>
      <span>{{ "telegram id:" + request.telegramId }}</span>
      <span>{{ "status:" + request.status }}</span>
      <span>{{ "amount:" + request.amount }}</span>
      <!-- Charges props -->
      <span v-if="requestType === 'charges'">{{ "type:" + request.type }}</span>
      <span v-if="requestType === 'charges'">{{
        "wallet:" + request.wallet
      }}</span>
      <!-- Payments props -->
      <span v-if="requestType === 'payments'">{{
        "number:" + request.number
      }}</span>
      <span v-if="requestType === 'payments'">{{
        "provider:" + request.provider
      }}</span>
      <span v-if="requestType === 'payments'">{{
        "number type:" + request.numType
      }}</span>
      <!-- Adsl props -->
      <span v-if="requestType === 'adsl'">{{
        "client:" + request.clientName
      }}</span>
      <span v-if="requestType === 'adsl'">{{
        "provider:" + request.provider
      }}</span>
      <span v-if="requestType === 'adsl'">{{ "number" + request.number }}</span>
      <span>{{ "current cash for this user:" + currentCash }}</span>
      <span>{{ "requested in:" + request.createdAt }}</span>
    </p>
    <div class="tools" v-if="requestStatus === 'pending' && shownTools">
      <input type="text" v-model="reason" />
      <button class="completed" @click="answerRequest('yes')">✔</button>
      <button class="rejected" @click="answerRequest('no')">✘</button>
    </div>
  </article>
</template>

<script>
import axios from "axios";
export default {
  name: "request",
  props: ["stringRequest", "requestType"],
  data() {
    return {
      reason: "",
      shownTools: true,
      request: JSON.parse(this.stringRequest),
      currentCash: "",
      currentHold: "",
    };
  },
  async mounted() {
    let cash = await axios.get(
      `${this.$store.state.server}/cash/${this.request.telegramId}`
    );
    this.currentCash = cash.data;
  },
  methods: {
    async answerRequest(decision) {
      this.shownTools = false;
      let confirmationMessage =
        decision === "yes"
          ? `تم تنفيذ طلبكم السابق الواقع في ${this.request.createdAt}`
          : `تم رفض طلبكم السابق الواقع في ${this.request.createdAt}`;
      try {
        switch (this.requestType) {
          case "payments":
          case "adsl":
            if (this.currentCash < this.request.amount) {
              alert("User's cash is less than the charged amount");
              return;
            }
            break;
          case "charges":
            if (decision === "yes") {
              this.reason = Number.parseInt(this.reason); // to present the amount in the same field of the amount.
            }
            break;
        }
        axios.put(
          `${this.$store.state.server}/${this.requestType}/${this.request._id}/${decision}`,
          {
            reason: this.reason, // represents reason of refusing and amount when accepting
          }
        );

        axios.post(
          `${this.$store.state.server}/broadcast/${this.request.telegramId}`,
          {
            confirmationMessage,
          }
        );
      } catch (err) {
        console.log(err);
        alert(
          "SOMETHING WENT WRONG PLEASE TRY AGAIN, AND CHECK YOUR CONNECTION OR PASSWORD"
        );
      }
    },
  },
};
</script>

<style lang="scss" scoped>
article {
  background: rgb(228, 228, 228);
  border-radius: 8px;
  padding: 10px;
  p {
    text-align: left;
    white-space: pre-wrap;
    line-height: 1.5em;
    span {
      display: block;
    }
  }
  .tools {
    display: flex;
    flex-flow: row wrap;
    .completed {
      background: green;
    }
    .rejected {
      background: red;
    }
    .completed,
    .rejected {
      color: white;
      border-radius: 4px;
      border: none;
      cursor: pointer;
    }
  }
}
</style>
