<template>
  <div class="charges">
    <section>
      <request
        v-for="request in requests"
        :key="request._id"
        :stringRequest="JSON.stringify(request)"
        requestType="charges"
      />
    </section>
  </div>
</template>

<script>
import axios from "axios";
import request from "../components/request.vue";
import playAlert from "alert-sound-notify";

export default {
  name: "Charges",
  data() {
    return {
      requests: [],
    };
  },
  components: { request },
  methods: {
    async getRequests() {
      let rs0 = await axios.get(`${this.$store.state.server}/charges/0`);
      if (
        !this.requests[0] ||
        this.requests[0].createdAt !== rs0.data.createdAt
      ) {
        let rs = await axios.get(`${this.$store.state.server}/charges`);
        this.requests = rs.data;
        playAlert("submarine");
      }
    },
  },
  mounted() {
    this.getRequests();
    setInterval(() => {
      this.getRequests();
    }, 180000);
  },
};
</script>

<style lang="scss" scoped>
section {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 10px;
}

@media (min-width: 800px) {
  section {
    grid-template-columns: 1fr 1fr;
  }
}
</style>
